@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
* {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 5px;
  background-color: gainsboro;
}

/* BASIC STRUCTURE */
.container {
  display: flex;
}
.container > section {
  width: 66%;
  background-color: white;
}
.container > section.side-section {
  width: 20%;
  background-color: white;
  min-width: 160px;
  min-height: 98vh;
}
.side-section.records,
.mini-records {
  margin-right: 10px;
}
.side-section.annotations,
.mini-annotations {
  margin-left: 10px;
}
.section-head,
.ham-btn {
  display: flex;
  align-items: center;
}
.ham-btn {
  display: flex;
}
.side-section-head {
  justify-content: space-between;
  border: solid 1px black;
}
.section-head {
  background-color: rgb(84, 65, 207);
  color: white;
  padding: 10px 5px;
}
.section-head > h3 {
  margin: 0;
}
.mini-side {
  background-color: rgb(84, 65, 207);
  writing-mode: vertical-lr;
  text-orientation: upright;
  display: flex;
  align-items: center;
  color: white;
  padding: 5px;
  justify-content: center;
  height: 96vh;
  width: 30px;
  cursor: pointer;
}
.mini-side > .ham-btn {
  position: absolute;
  top: 10px;
  left: 10px;
}

/* RECORD SECTION */
.record-item {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border-bottom: solid 1px gainsboro;
  padding: 10px 5px;
  background-color: lavenderblush;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.record-item-selected {
  background-color: rgb(205, 205, 255);
}
.record-item:hover {
  background-color: rgb(205, 205, 255);
}

/* MAIN SECTION */
.section-main {
  flex-grow: 2;
}
.section-main > .section-head {
  padding: 10px;
}
.input-text-container {
  padding: 10px;
}
.annotation.highlight {
  background-color: rgb(205, 205, 255);
}
.annotation.highlight > small {
  color: rgb(84, 65, 207);
}
.btn-main {
  margin-right: 5px;
  color: rgb(84, 65, 207);
  font-weight: bold;
  background-color: white;
  border: none;
  border-radius: 2px;
  padding: 4px 8px;
  cursor: pointer;
}
.main-info {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

/* ANNOTATION SECTION */
.annotation-item {
  font-size: 0.8rem;
  padding: 10px 5px;
  display: flex;
  justify-content: space-around;
}
.annotation-remove {
  color: rgb(84, 65, 207);
  cursor: pointer;
}

/* RESPONSIVE CSS */
@media screen and (max-width: 784px) {
  .main-info,
  .main-info > input {
    display: block;
  }
}
@media screen and (max-width: 600px) {
  .mini-records {
    margin-right: 5px;
  }

  .mini-annotations {
    margin-left: 5px;
  }
}
